import { BrowserRouter, Routes, Route, useLocation, useEffect } from 'react-router-dom';
import './App.css';
import Home from './pages/Home';
import About from './pages/About';
import Sidebar from './components/Sidebar';
import Login from './pages/Login';
import SearchOnPerson from './pages/SearchOnPerson';
import SummaryReport from './components/SummaryReport';
import SearchResult from './components/SearchResult';
import AccountsProfile from './pages/AccountsProfile';
import SignUp from './pages/SignUp';
import CustomerAdminScreen from './pages/CustomerAdminScreen';

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route>
          <Route path="/" element={<Home />} />
          <Route path="/home" element={<Home />} />
          <Route element={<Login />} path="/login" />
          <Route element={<SignUp />} path="/signup" />
          <Route element={<MainLayout />} path="/*" />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
function MainLayout() {
  const location = useLocation();

  // Check if the current path is either "/" or "/home"
  const isHome = location.pathname === "/" || location.pathname === "/home";

  return (
    <div>
      <Sidebar>
        <Routes>
          <Route element={<SearchOnPerson />} path="/searchOnPerson" />
          <Route element={<SummaryReport />} path="/SummaryReport" />
          <Route element={<SearchResult />} path="/SearchResult" />
          <Route element={<AccountsProfile />} path="/AccountsProfile" />
          <Route element={<CustomerAdminScreen />} path="/CustomerAdmin" />
        </Routes>
      </Sidebar>
    </div>
  );
}
export default App;
