import React, { useState, useEffect } from 'react'
import './Sidebar.css'
import { NavLink } from 'react-router-dom'
import probeDigitalCollapsed from '../assets/images/icons/probe-digital-collapsed-icon.svg'
import probeDigitalLogo from '../assets/images/icons/probe-digital-logo.svg'
import searchOnPerson from '../assets/images/icons/search-on-person.svg'
import leftArrowMenu from '../assets/images/icons/left-arrow-menu.svg'
import rightArrowMenu from '../assets/images/icons/right-arrow-menu.svg'
import CorporateIcon from '../assets/images/icons/Corporate.svg'
import OwnerShipIcon from '../assets/images/icons/Ownership.svg'
function Sidebar({ children }) {
    const [isOpen, setIsOpen] = useState(false)
    useEffect(() => {
        function handleResize() {
            if (window.innerWidth <= 768) {
                setIsOpen(false);
            } else {
                setIsOpen(true);
            }
        }

        handleResize();

        window.addEventListener('resize', handleResize);

        return () => window.removeEventListener('resize', handleResize);
    }, []);

    const toggle = () => setIsOpen(!isOpen)
    const menuItem = [
        {
            path: "/searchOnPerson",
            name: "Corporate Links",
            icon: CorporateIcon
        },
        {
            path: "/AccountsProfile",
            name: "Foreign UK Ownership",
            icon: OwnerShipIcon
        },
        {
            path: "/",
            name: "Background Investigation",
            icon: searchOnPerson
        },

    ]
    return (
        <div className='sidebar-outer'>
            <div>
                <div className={isOpen ? "sidebar sidebar-opened" : "sidebar sidebar-closed"} >
                    <div className='top_section'>
                        <div className='logo'>
                            <img alt='icon here' src={isOpen ? probeDigitalLogo : probeDigitalCollapsed} />
                        </div>
                        <div style={{ marginLeft: isOpen ? "50px" : "0px" }} className='bars'>
                            <button className='menu-btn' style={{ right: isOpen ? "0px" : "-32px" }} onClick={toggle}>
                                <img alt='icon here' src={isOpen ? leftArrowMenu : rightArrowMenu} />
                            </button>
                        </div>
                    </div>
                    {
                        menuItem.map((item, index) => (
                            <NavLink to={item.path} key={index} className="link" activeclassName="active">
                                <div className='icon'>
                                    <img alt='icon here' src={item.icon} />
                                </div>
                                <div style={{ display: isOpen ? "block" : "none" }} className='link_text small-text'>
                                    {item.name}
                                </div>
                            </NavLink>
                        ))
                    }
                </div>
            </div>
            <main>
                {children}
            </main>
        </div>
    )
}

export default Sidebar